.fulpage_slider .slide__fp:last-child {
  border-right: none;
}

.fulpage_slider .slide__fp .content {
  position: absolute;
  bottom: 0;
  visibility: hidden;
  margin: 40px;
  padding: 30px;
  opacity: 0;
  background-color: #000;
  color: #fff;
  transition: 0s;
  transform: tranlateY(100px);
}

.fulpage_slider .slide__fp:hover .content {
  visibility: visible;
  opacity: 1;
  transition: .5s;
  transition-delay: .5s;
  transform: tranlateY(0);
}

