  border-radius: 10px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
}

.contenting_box {
  max-width: 600px;
  text-align: center;
}

.contenting_box h2 {
  font-size: 10vw;
  color: #fff;
  line-height: 1em;
}

.contenting_box h4 {
  font-size: 2vw;
  color: #fff;
  line-height: 1em;
}

footer {
  height: 100vh;
  background-color: red;
}

.header_wavy {
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: #111;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header_wavy svg {
  position: absolute;
  top: 0;
  left: 0;
}

.clock_block {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.clock_block__items {
  width: 50%;
}

.countdoun {
  display: flex;
  margin-top: 50px;
}

.countdoun div {
  position: relative;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  background: #333;
  margin: 0 15px;
  font-size: 3rem;
  font-weight: 500;
  color: #fff;
}

.countdoun div:before {
  content: '';
  position: absolute;
  bottom: -30px;
  left: 0;
  width: 100%;
  height: 35px;
  background-color: #ff0;
  color: #333;
  font-size: .9800rem;
  line-height: 35px;
  font-weight: 300;
}

.countdoun #day:before {
  content: 'Дней';
}

.countdoun #hour:before {
  content: 'Часов';
}

.countdoun #minute:before {
  content: 'Минут';
}

.countdoun #second:before {
  content: 'Секунд';
}

